<template>
    <a href="javascript:;" class="hc-list-wrap">
        <div class="hc-wrap-item">
            <div class="wrap-item-title">
                <slot name="icon"></slot>
                <span class="item-title-text">{{ title }}</span>
            </div>
            <div class="wrap-item-value">
                <span>{{ value }}</span><slot name="extend" class="wrap-item-value-ex"></slot>
            </div>
            <i class="wrap-item-allow-right"></i>
        </div>
    </a>
</template>
<script>

export default {
    props:{
        title:String,
        value:String
    }
};
</script>

<style lang='scss'>
.hc-list-wrap {
  background-color: #fff;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: inherit;
  min-height: 48px;
  display: block;
  overflow: hidden;
  position: relative;
  text-decoration: none;
  .hc-wrap-item {
    background-image: -webkit-gradient(
      linear,
      left top,
      left bottom,
      from(#d9d9d9),
      color-stop(50%, #d9d9d9),
      color-stop(50%, transparent)
    );
    background-image: linear-gradient(
      180deg,
      #d9d9d9,
      #d9d9d9 50%,
      transparent 50%
    );
    background-size: 120% 1px;
    background-repeat: no-repeat;
    background-position: top left;
    background-origin: content-box;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: 16px;
    line-height: 1;
    min-height: inherit;
    overflow: hidden;
    padding: 0 10px;
    width: 100%;
    .wrap-item-title {
      -webkit-box-flex: 1;
      -ms-flex: 1;
      flex: 1;
    }
    .wrap-item-value {
      margin-right: 24px;
      color: #888;
    }
    .wrap-item-value-ex{
        display: inline-block;
    }
    .wrap-item-allow-right {
      &:after {
        border: solid 2px #c8c8cd;
        border-bottom-width: 0;
        border-left-width: 0;
        content: " ";
        top: 50%;
        right: 20px;
        position: absolute;
        width: 5px;
        height: 5px;
        -webkit-transform: translateY(-50%) rotate(45deg);
        transform: translateY(-50%) rotate(45deg);
      }
    }
  }
}
</style>



