<template>
   <div class="hc-mine-member">
	<div class="hc-mine-header">
        <a class="hc-member-info">
            <img class="hc-member-img" src="../../assets/img/mine/header-bg.png" alt="">
            <h3 class="hc-member-name">夏天蓝蓝的草原</h3>
            <p class="hc-member-lv">【普通会员】</p>
        </a>
    </div>
    <div class="hc-main-content">
        <div class="hc-row-item">
            <List title='我的订单' value='查看全部订单'>
                <svg slot="icon" class="icon hc-f15" aria-hidden="true">
                    <use xlink:href="#icon-ziyuan28"></use>
                </svg>
            </List>

            <div class="hc-nav-order">
                <a href="" class="hc-tab-item">
                    <div class="hc-nav-wrap">
                        <div class="hc-nav-img">
                            <svg class="icon hc-f20" aria-hidden="true">
                            <use xlink:href="#icon-ziyuan26"></use>
                        </svg>
                        </div>
                        <div class="hc-nav-desc">
                            待接单
                        </div>
                    </div>
                </a>
                <a href="" class="hc-tab-item">
                    <div class="hc-nav-wrap">
                        <div class="hc-nav-img">
                            <svg class="icon hc-f25" aria-hidden="true">
                            <use xlink:href="#icon-ziyuan25"></use>
                        </svg>
                        </div>
                        <div class="hc-nav-desc exception">
                            配送中
                        </div>
                    </div>
                </a>
                <a href="" class="hc-tab-item">
                    <div class="hc-nav-wrap">
                        <div class="hc-nav-img">
                            <svg class="icon hc-f19" aria-hidden="true">
                            <use xlink:href="#icon-ziyuan27"></use>
                        </svg>
                        </div>
                        <div class="hc-nav-desc">
                            已完成
                        </div>
                    </div>
                </a>
            </div>
            <div class="hc-whitespace-md"></div>
            <List title='积分管理' value='当前积分' @click.native="toIntegral">
                <svg slot="icon" class="icon hc-f17" aria-hidden="true">
                    <use xlink:href="#icon-ziyuan23"></use>
                </svg>
                <span slot="extend" class="hc-member-score">985</span>
            </List>
            <List title='收货地址管理' value=''>
                <svg slot="icon" class="icon hc-f18" aria-hidden="true">
                    <use xlink:href="#icon-lujing6"></use>
                </svg>
            </List>

            <div class="hc-whitespace-md"></div>

            <List title='设置' value=''>
                <svg slot="icon" class="icon hc-f15" aria-hidden="true">
                    <use xlink:href="#icon-ziyuan24"></use>
                </svg>
            </List>
        </div>
    </div>
   </div>
</template>
<style lang="scss">
@import "../../assets/css/common.scss";
.hc-mine-header {
  padding-top: px2rem(20);
  height: px2rem(185);
  text-align: center;
  background-image: url("../../assets/img/mine/header-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top left;
  background-color: #fff;
  .hc-member-info {
    display: inline-block;
    .hc-member-img {
      width: px2rem(80);
      height: px2rem(80);
      border: px2rem(5) solid #fff;
      border-radius: 50%;
      background-image: url("../../assets/img/mine/header-bg.png");
      background-repeat: no-repeat;
      background-size: cover;
      background-position: top left;
    }
    .hc-member-name {
      font-size: 16px;
    }
    .hc-member-lv {
      margin: px2rem(5) 0 0 0;
    }
  }
}
.hc-main-content {
  background: #f1f1f1;
  .hc-row-item a {
    border-bottom: 0.01rem solid #eee;
    .hc-member-score {
      color: #f00;
    }
  }
  .hc-nav-order {
    background: #fff;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    text-align: center;
    .hc-tab-item {
      display: block;
      padding: 20px 0;
      -webkit-box-flex: 1;
      -ms-flex: 1;
      flex: 1;
      text-decoration: none;
      color: #b3b3b3;
      .hc-nav-desc {
        margin-top: 10px;
        color: #333;
      }
      .exception {
        margin-top: 3px;
      }
    }
  }
}
</style>
<script>
import List from "../../components/common/list/List";
import { productList } from "../../api/axios.js";
export default {
  name: "mine",
  components: {
    List
  },
  data() {
    return {
      score: 9,
      result: {}
    };
  },
  methods: {
    handleItem(item) {
      this.result.categoryId = item.id;
      // this.getProductList();
    },
    getProductList() {
      productList().then(result => {
        console.info(result);
        this.result = result.result;
      });
    },
    toIntegral() {
      this.$router.push({path: 'mdcMine/integral'})
    }
  },
  mounted() {
    this.getProductList();
  }
};
</script>





