<template>
  <div class="main_page">
    <!-- 子页面 主路由 -->
    <transition :name="transitionName">
      <navigation>
        <router-view style="min-height:100vh;transition: all .3s cubic-bezier(.55,0,.1,1);"></router-view>
      </navigation>
    </transition>
    <transition>
      <div v-show="$store.state.common.isHome">
				<!-- 订单页面 -->
        <transition :name="$parent.transitionName">
          <div class="tab_item_page ">
            <MineCom />
          </div>
        </transition>
      </div>
    </transition>
  </div>
</template>

<script>
  import MineCom from '../../mdc/mine/mineCom'
  export default {
    name: 'home',
    data() {
      return {
        transitionName: 'fade',
        isScroll: true,
        showHome: false
      }
    },
    components: {
      MineCom
    },
    beforeRouteUpdate(to, from, next) {
      // 监听路由变化 进行动画设置
      let isBack = this.$router.isBack
      if (isBack) {
        this.transitionName = 'fade'
      } else {
        this.transitionName = 'fade'
      }
      this.$router.isBack = false
      next()
    }
  }
</script>
<style rel="stylesheet/scss" lang="scss" >
.mint-popup {
  width: 100%;
  height: 100%;
  overflow: scroll;
}

.main_page {
  .tab_item_page {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0px;
    right: 0px;
  }
  .icon_tabs {
    background-size: 100%;
  }
  .is-selected,
  .icon_tabs {
    transition: all ease .5s;
  }

  .mint-tab-item {
    color: rgb(189, 189, 189)
  }


}
</style>



